$mainColor: #f08098;
$middleMainColor: #f88aa2;
$lightMainColor: #f8c8c6;
$mainTranspColor: #f08098f5;
$lightColor: white;
$darkColor: #051225;
$flowerMainColor: #f08098;
$grey1Color: #808080;
$cinnamon-satin: #f092b7;
$silver-pink: #fcdee7;
$dark-olive-green: #95a351;
$rose-red: #e2316c;
$antique-bronze: #ffa3b7;

$circleColor: $lightMainColor;
$techColor: $mainColor;
$linkColor: #add8e6;
$topBarLinkColor: #051225;
$background1Color: #2b2b2b;
$background2Color: white;

//#fbd0da w #5f071c
//2e2f4a #212529
$headerFonts: 'Montserrat', Arial, Helvetica, sans-serif;
$header2Fonts: 'Montserrat', Arial, Helvetica, sans-serif;
$cardHeadFont: 'Montserrat', Arial, Helvetica, sans-serif;
$cardColor: #ffffff;
$cardColorDark: #f0ebf4;

$topBarHeight: 60px;
$pageHeight: calc(100vh - 60px);
$sidePadding: calc(0.03 * 100vw);
$topBottomPadding: 30px;
//@import '../../global.scss/';

$mobileWidth: 768px;

@mixin mobile {
    @media (max-width: #{$mobileWidth}){
        @content
    }
}

h2, h3, h4, h5, h6 {
  font-family: $header2Fonts;
  font-weight: 400;
  //font-weight: 400;
  //font-family: 'Libre Baskerville', serif;
  //font-family: 'Open Sans', sans-serif;
  //font-family: 'Source Sans Pro', sans-serif;
}

a, Link {
  color: $linkColor;
  font-family: $header2Fonts;
}

p {
  font-size: 18px;
}

h1 {
  text-transform: uppercase;
  font-family: $headerFonts;
  font-weight: 300;
  font-size: 50px;
  @include mobile{
    font-size: 40px;
  }
}
h2 {
  text-transform: lowercase;
  font-size: 30px;
  @include mobile{
    font-size: 25px;
  }
}
h3 {
  font-size: 25px;
  @include mobile{
    font-size: 20px;
  }
}

h4 {
  font-size: 20px;
}

h5 {
  font-size: 15px;
}

.cards {
  display: flex;
  flex-direction: column;
  justify-content: center;
  h2, h3, h4, h5 {
    font-weight: 600;
  }
  .container {
    width: 100vw;
    height: 100%;
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    .item {
      width: 700px;
      background-color: $cardColor;
      border-radius: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 15px 0;
      overflow: hidden;
      @include mobile {
        width: 80%;
      }
      .left {
        flex: 8;
        display: flex;
        align-items: center;
        justify-content: center;
        .leftContainer {
          width: 90%;
          display: flex;
          flex-direction: column;
          justify-content: center;
          margin: 10px 0;
            .imgContainer {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              //background-color: rgb(245, 179, 155);
              display: flex;
              align-items: center;
              justify-content: center;
              @include mobile {
                width: 20px;
                height: 20px;
              }
              img {
                width: 25px;
                @include mobile {
                  width: 15px;
                }
              }
            }
          h3 {
            font-family: $cardHeadFont;
            font-size: 30px;
            //margin: 5px 0;
            @include mobile{
              font-size: 25px;
            }
          }
          h4 {
            font-family: $cardHeadFont;
            font-size: 20px;
            margin: 10px 0;
            color: $techColor;
            @include mobile{
              font-size: 15px;
            }
          }
          h5 {
            font-family: $cardHeadFont;
            font-size: 18px;
            margin-bottom: 5px;
            @include mobile{
              margin-bottom: 0;
              font-size: 13px;
            }
          }
          p {
            //font-size: 18px;
          }
          span {
            font-size: 12px;
            font-weight: 500;
            text-decoration: underline;
            cursor: pointer;
          }
        }
      }
      .right {
        flex: 4;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        img {
          width: 100%;
          object-fit: cover;
        }
        @include mobile {
          display: none;
        }
      }
    }
  }
}


.textAndPicture {
  margin: $topBottomPadding;
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow-x: hidden;
  width:100%;
  @include mobile {
    flex-direction: column;
  }
  .left {
    flex: 2;
    overflow: hidden;
    margin: 20px;
    img {
      height: 100%;
      @include mobile {
        height: 150px;
      }
    }
  }
  .right {
    flex: 3;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    @include mobile {
      align-items: center;
    }
    p {
      //font-size: 18px;
      padding: 10px 30px;
    }
    .horizList {
      flex-direction: row;
      padding: 10px 30px;
      .title {
        //font-weight: 500;
      }
      p {
        //font-size: 18px;
        padding: 0;
      }
    }
    .links {
      h3 {
        margin: 0 10px;
      }
      display: flex;
      flex-direction: row;
      align-content: center;
      padding: 10px 20px;
      .icon {
        font-size: 30px;
        margin: 0 10px;
      }
      a {
        //font-size: 30px;
        text-decoration: none;
        color: $topBarLinkColor;
        //margin: 0 10px;
      }
      a:hover {
        color: $linkColor;
      }
    }
  }
}